        <div class="cover">
          <img class="auto-img" :src="m.cover">
        </div>

        <div class="nav">
          <div class="title">{{m.title}}</div>
          <div class="author">作者:{{m.author}}</div>
          <div class="classify">分类:{{m.category}}</div>
          <div class="text">简介:{{m.desc}}</div>
          <div class="chapter">章节:{{m.first_chapter}}</div>
        </div>

