<script>
  const url = "https://www.apiopen.top/novelInfoApi?name=游戏竞技"

 
  export default{
    name:'HelloWorld',
    data(){
      return {
        main:{}
      }
    },
     methods:{
      go(b,t,c,ca,f,d,a){
      console.log(b)
      console.log(t)

      let soft = 'https://m.kayege.com/book/' + b
      this.$router.push({name:'search',params:{title:t,bid:b,cover:c,category:ca,author:a,desc:d,first_chapter:f}})
      }
    },
    created() {
      this.axios.get(url).then(response => {
        this.main = response.data.data.data
        // eslint-disable-next-line
        console.log(this.main)
        console.log(response.data.data.data)
      }, err => {
        alert(err)
      })
    }
  }
</script>

