import Vue from 'vue'
import Router from 'vue-router'
import Home from './components/HelloWorld.vue'
import vip from './components/VIP.vue'
import li from './components/分类.vue'
import pa from './components/排行.vue'
import city from './components/city.vue'
import game from './components/game.vue'
import lingyi from './components/lingyi.vue'
import yanqing from './components/yanqing.vue'
import erciyuan from './components/erciyuan.vue'
import wenxue from './components/wenxue.vue'
import search from './components/search.vue'
import searchItem from './components/search-item.vue'


Vue.use(Router)

const routes = [
     {path:"/", component:Home},
     {path:"/vip", component:vip},
     {path:"/li", component:li},
     {path:"/pa", component:pa},
     {path:"/city", component:city},
     {path:"/game", component:game},
     {path:"/lingyi", component:lingyi},
     {path:"/yanqing", component:yanqing},
     {path:"/erciyuan", component:erciyuan},
     {path:"/wenxue", component:wenxue},
     {name:"search",path:"/search", component:search},
     {name:"search-item",path:"/search-item", component:searchItem},
  ]



const router =  new Router({
   routes
})

export default router
