<template>
  <div class="hello">

    <div class="container" v-for="m in main">
    	<div class="tab-item" @click="go(m.bid,m.title,m.cover,m.category,m.first_chapter,m.desc,m.author)">
    		
    		<div class="cover">
    			<img class="auto-img" :src="m.cover">
    		</div>

    		<div class="nav">
    			<div class="title">{{m.title}}</div>
    			<div class="author" @click="go">作者:{{m.author}}</div>
    			<div class="classify">分类:{{m.category}}</div>
    			<div class="text">简介:{{m.desc}}</div>
    			<div class="chapter">章节:{{m.first_chapter}}</div>
    		</div>

    		<div class="loader">加载中</div>
    	</div>
    </div>

    
  </div>
</template>


<script type="text/javascript">
	





 export default {
   name:'search-item',
    data(){
      return {
        main:{},
        name:{}
      }
    },
    methods:{
    	go(b,t,c,ca,f,d,a){
      console.log(b)
      console.log(t)

      this.$router.push({name:'search',params:{title:t,bid:b,cover:c,category:ca,author:a,desc:d,first_chapter:f}})
      }
    },
    created() {
    	let address = this.$route.params.c
    	this.axios.get(address).then(response => {
    		this.main = response.data.data.data
    		console.log(this.main)
    	},err => {
    		alert(err)
    	})
    }
 }


</script>



<style lang="scss" rel="stylesheet" scoped>
 .hello{
   background:#fff;
   margin:8px;
 }
   
   .container{

      .tab-item{
        display:flex;
        align-items:flex-start;
        font-size:0;
        width:100%;
        height:100%;
        border-bottom:1px solid #E9C7C1;
        .cover{
         width:100px;
         margin:10px;
         display:inline-block;
        }
        .nav{
          display:inline-block;
        
          .title{
            font-size:20px;
            display:flex;
            margin:7px 0;
            color:#666666;
          }
          .author{
             font-size:12px;
             display:flex;
             margin:5px 0;
             color:#666666;
          }
          .classify{
             font-size:12px;
             display:flex;
             margin:5px 0;
             color:#666666;
          }
          .text{
            font-size:12px;
            display:flex;
            width:200px;
            height:15px;
            overflow:hidden;
            text-overflow:ellipsis;
            margin:5px 0;
            color:#666666;
          }
          .chapter{
            font-size:12px;
            display:flex;
            margin-bottom:15px;
            color:#666666;
          }
          
        }
        
      }
   }

   
</style>

<style>

  .auto-img{
    width: 100%;
    height: 100%;
    display: block;
  }
</style>